import React, { useState } from 'react'
import ResizableImg from './ResizeableImg'
import { Rnd } from 'react-rnd'
import { Box, Image, Input, Button, Text, Grid, GridItem } from '@chakra-ui/react'

const style = {
    display: "flex",
    alignItems: "center",
    justifyContent: "center",
    background: "none "
  };


function AddImgButton() {

    const [val, setVal]=useState([]);
    const handleAdd=()=>{
        const abc=[...val,[]]
        setVal(abc)
    }
    const handleChange=(onChangeValue,i)=>{
        const inputdata = [...val]
        inputdata[i]=onChangeValue.target.value
        setVal(inputdata)
    }

    const handleDelete=(i)=>{
        const deleteVal=[...val]
        deleteVal.splice(i,1)
        setVal(deleteVal)
    }

    
    console.log(val, "data")
    return(
        <>
        <Button ml="24px" size="xs" onClick={()=>handleAdd()}>Add Image</Button>
            {val.map((data,i)=>{
                return(
                    <div>
                        <Text margin="2px" fontSize={"xs"}>Image {i}
                        <Input marginLeft="12px" width="40%" h="24px" value={data} onChange={e=>handleChange(e,i)}/>
                        <Button marginLeft="4px" marginTop="-8px" size="xs" h="20px" onClick={()=>handleDelete(i)}>x</Button>
                        </Text>
                        
                        
                        <Rnd
                            style={style}
                            default={{
                            x: 400,
                            y: 0,
                            width: 320,
                            height: 320
                        }}
                        >
                        
                        <Box borderRadius={10} boxShadow="dark-lg" margin="0" bg={"grey"} width="100%" height="100%">
                        <Box borderRadius={10} margin="0"  width="100%" height="90%" ><Text textAlign="center" fontSize={"xs"}>Image {i}</Text>
                        <ResizableImg imgurl={data}/></Box>
                        
                        <Box borderRadius={10}  width="100%" height="10%">
                        <Grid ml="5%"  width="95%" height="95%" templateColumns='repeat(6,1fr)' templateRows='repeat(1,1fr)'>
                            <GridItem colSpan={5}>
                                <Input borderRadius={10} ml="0"  w="100%" height="90%"  placeholder='Enter Image URL' value={data} onChange={e=>handleChange(e,i)}/>
                            </GridItem>
                            <GridItem colSpan={1} >
                                <Box borderRadius={10}  width="100%" height="100%"><Button w="70%" h="80%" onClick={()=>handleDelete(i)}>x</Button></Box>
                            </GridItem>
                        </Grid>
                        
                        
                        </Box>
                        </Box>
                        </Rnd>
                    </div>
                )
            })}
        </>
    )
}

export default AddImgButton