import { Container, Input, Text, Button, Box, Grid, GridItem, ChakraProvider, boxShadow, Textarea } from '@chakra-ui/react';
import React, { useState } from 'react'
import { Rnd } from 'react-rnd';

const style = {
    display: "flex",
    alignItems: "center",
    justifyContent: "center",
    background: "none"
  };            

function AddTextButton() {

    const [val, setVal]=useState([]);
    const handleAdd=()=>{
        const abc=[...val,[]]
        setVal(abc)
    }
    const handleChange=(onChangeValue,i)=>{
        const inputdata = [...val]
        inputdata[i]=onChangeValue.target.value
        setVal(inputdata)
    }

    const handleDelete=(i)=>{
        const deleteVal=[...val]
        deleteVal.splice(i,1)
        setVal(deleteVal)
    }

    console.log(val, "data")
    return(
        <>
        
        <Button ml="24px" size="xs" onClick={()=>handleAdd()}>Add Text</Button>
            {val.map((data,i)=>{
                return(
                    <div>
                        <Text margin="2px" fontSize={"xs"}>Text {i}
                        <Input marginLeft="12px" width="40%" h="24px" value={data} onChange={e=>handleChange(e,i)}/>
                        <Button marginLeft="4px" marginTop="-8px"  size={"xs"} h="20px" onClick={()=>handleDelete(i)}>x</Button>
                        </Text>
                        
                        <Rnd style={style}
                            default={{
                            x: 450,
                            y: 0,
                            width: 320,
                            height: 320
                        }}>
                        <Box boxShadow={"dark-lg"} borderRadius={10} margin="0" bg={"grey"} width="100%" height="100%">
                        <Box borderRadius={10} margin="0"  width="100%" height="8%" ><Text h="16px" textAlign="center" fontSize={"xs"}>Text {i}</Text>
                        
                        </Box>
                        <Textarea resize="none" w="100%" h="90%" fontSize="xl">{data}</Textarea>
                        </Box>
                        </Rnd>
                    </div>
                )
            })}
        </>
    )
}

export default AddTextButton